import React, { useState, useEffect } from 'react';
import NewsCard from './NewsCard'
import data from '../data'
import './News.css'

function GetDateTime(SK){
    return (
        SK.split('_')[0]
    )
}

function News() {
    const [items, setItems] = useState([]);

    useEffect(() => {
        const currentDate = new Date().toJSON().slice(0, 10);
        fetch("https://ft5f6uj1n2.execute-api.eu-west-3.amazonaws.com/Prod/" + currentDate)
            .then(res => res.json())
            .then(setItems);
    }, []);

    return (
        <div className="news-flex">
            {items.length > 0 ? items.map((item) =>
                (
                    <NewsCard
                        key={item.uuid}
                        title={item.title}
                        description={item.contentSnippet}
                        link={item.link}
                        sourceName={item.source_name}
                        dateTime={GetDateTime(item.SK)}
                    />
                )
            ) :
                <h2>Loading ...</h2>
            }
        </div>
    )
}

export default News


